.container {
  font-family: 'Josefin Sans', sans-serif;
  height: 100%;
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

  @media only screen and (max-width: 600px) {
    width: 100%;
  }

  @media only screen and (min-width: 1600px) {
    width: 65%;
  }
}
