@import '@styles';

.skill {
  max-width: 340px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: $textColor;
  padding: 20px 0;

  .icon {
    color: $primaryColor;
    height: 80px;
    width: 80px;
    margin: 0px auto 30px;
    background: rgba(255, 255, 255, 0.1);
    display: block;
    line-height: 80px;
    font-size: 50px;
    box-shadow: -6px 10px 40px rgba(0, 0, 0, 0.06);
    text-align: center;
    transform: rotate(45deg);
    transform-origin: center;
    .logo {
      transform: rotate(-45deg);
      color: $primaryColor;
      padding: 6px 0 0 6px;
    }
  }
}
