@import '@styles';

.footer {
  height: 200px;
  background-color: $bgColor;
  color: #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 30px 0;
  .container {
    flex-direction: column;
    .list {
      width: 330px;
      display: flex;
      justify-content: space-between;
      & a i {
        color: $primaryColor;
        font-size: 35px;
      }
    }
  }
}
