@import '@styles';

.wrapper {
  width: 300px;
  padding: 45px;
  position: fixed;
  color: $textColor;
  border: solid black;
  background: rgba($color: #000000, $alpha: 0.5);
  z-index: 10;
  margin: 0 auto;
  top: 32%;
  left: 0;
  right: 0;
  text-align: center;
  @media only screen and (max-width: 500px) {
    width: 220px;
  }
  .block {
    position: relative;
    .icon {
      color: $primaryColor;
      font-size: 70px;
    }
    .message {
      font-size: 27px;
    }
    & button {
      position: absolute;
      background: none;
      border: none;
      color: $primaryColor;
      font-size: 34px;
      right: -66px;
      top: -61px;
    }
  }
}
